:root {
    /* Typography */
    --font-family-primary: "Space Grotesk", sans-serif;
    --font-family-secondary: "Space Mono", monospace;

    --weight-light: 300;
    --weight-regular: 400;
    --weight-medium: 500;
    --weight-bold: 700;

    --size-xs: 12px;
    --size-s: 14px;
    --size-m: 16px;
    --size-l: 18px;

    /* Colors */
    --color-bg-primary: #0a0a0b;
    --color-bg-secondary: #131314;
    --color-bg-tertiary: #1d1d1f;
    --color-bg-quaternary: #020203;

    --color-text-primary: #fff; 
    --color-text-secondary: rgba(255, 255, 255, 0.5);

    /* Spaces */
    --space-s: 2px;
    --space-2-s: 4px;
    --space-3-s: 6px;
    --space-4-s: 8px;
    --space-6-s: 12px;
    --space-8-s: 16px;
    --space-10-s: 20px;
    --space-12-s: 24px;
    --space-16-s: 32px;
    --space-24-s: 48px;
    --space-32-s: 64px;

    --border-radius-3-s: 6px;
    --border-radius-4-s: 8px;
    --border-radius-5-s: 10px;
}

* {
    box-sizing: border-box;
}

html, body {
    width: 100%;
    height: 100%;
}

#root {
    width: 100%;
    height: 100%;
    
    background-color: var(--color-bg-primary);
    display: flex;
    justify-content: center;

    font-family: var(--font-family-primary);
    font-weight: var(--weight-regular);
    font-size: var(--size-m);
}
